<template>
  <div class="upBankCard">
    <div class="upBankCard-title">银行卡认证</div>
    <div class="upBankCard-input">
      <van-cell-group>
        <van-field v-model="bankCardId" placeholder="请输入银行卡号" @blur="inputBankCardId" :error-message="bankCardIdError">
          <img class="nameClass" slot="left-icon" src="../../../assets/registerIcon/bank.png" alt="">
        </van-field>
        <van-field v-model="bankCardName" :placeholder="Bank" :error="errorBank" disabled>
          <img class="nameClass" slot="left-icon" src="../../../assets/registerIcon/bankName.png" alt="">
        </van-field>
      </van-cell-group>
    </div>
    <div class="upBankCard-upImg">
      <div class="upId-upImg-box">
        <van-uploader preview-image :after-read="upBankCard">
          <img  class="uploader-img" src="../../../assets/registerIcon/upImgBox.png" />
          <img ref="bankCard" class="uploader-img2" src="../../../assets/registerIcon/upBank.png" />
        </van-uploader>
      </div>
      <div class="uploader-text">点击上传银行卡正面</div>

      <!--      <div>-->
<!--        <van-uploader  accept="image/*" preview-image :after-read="upBankCard">-->
<!--          <img ref="bankCard" class="uploader-img" src="../../../assets/registerIcon/bankCard.png" />-->
<!--        </van-uploader>-->
<!--      </div>-->
    </div>
    <div class="upBankCard-Tips">
      <p>温馨提示</p>
      <p>1.请确保银行卡边框完整、文字清晰、无反光</p>
      <p>2.请确保注册手机号与银行卡预存手机一致</p>
      <p>3.请使用银行卡原件拍摄,本平台会加密存储您的身信息,仅用于开户使用</p>
    </div>
    <div class="upBankCard-statement">
      <van-checkbox v-model="statement" checked-color="#FFC220" >已阅读并同意<span style="color: #FFC220">《期货开户及版权声明》</span></van-checkbox>
    </div>
    <div class="upBankCard-but-box">
      <van-button v-if="showBut" class="upBankCard-but" :disabled="!statement" @click="getLogin">申请开户</van-button>
    </div>

  </div>
</template>

<script>
import { bankCardAttribution } from '../../../utils/bankCardAttribution'
import { Toast } from 'vant'
import Exif from 'exif-js'
export default {
  name: 'upBankCard',
  data () {
    return {
      showBut:true,
      // 开户行名称
      bankCardName: '',
      // 银行卡号
      bankCardId: '',
      bankCardIdError:'',
      // 是否同意声明
      statement: true,
      Bank: '开户行',
      errorBank: false,
      // 图片上传
      // 上传身份证正面
      files: {
        name: "",
        type: ""
      },
      headerImage: null,
      picValue: null,
      PicValue: null,
      upImgUrl:'http://47.74.187.199:7777/api/file/uploadImg',
      bankIdName:'',
    }
  },
  created(){
    console.log(this.$route.params)
  },
  methods: {
    // 图片上传后的回调函数--身份证正面
    upBankCard (file) {
      this.$refs.bankCard.src = file.content
      this.files.name = file.file.name; // 获取文件名
      this.files.type = file.file.type; // 获取类型
      this.picValue = file.file; // 文件流
      this.imgPreview(this.picValue);
      Toast.loading({
        duration:0,
        message:'图片上传中...'
      })
    },
    linkUpId () {
      this.$router.push({
        path: '/register/upBankCard'
      })
    },
    inputBankCardId(){
      let bankCardValue = bankCardAttribution(this.bankCardId)
      if (bankCardValue === 'error') {
        this.bankCardIdError = '请输入正确的银行卡号码'
        this.bankCardName = ''
        this.errorBank = true
        this.showBut = false
      } else {
        this.bankCardIdError = ''
        this.bankCardName = bankCardValue.bankName
        this.errorBank = false
        this.showBut = true
      }
    },
    // // 跳转到审核页面
    // linkExamine () {
    //   this.$router.push({
    //     name: 'examine'
    //   })
    // },
    // 图片处理
    imgPreview(file) {
      let self = this;
      let Orientation;
      //去获取拍照时的信息，解决拍出来的照片旋转问题
      Exif.getData(file, function () {
        Orientation = Exif.getTag(this, "Orientation");
      });
      // 看支持不支持FileReader
      if (!file || !window.FileReader) return;
      if (/^image/.test(file.type)) {
        // 创建一个reader
        let reader = new FileReader();
        // 将图片2将转成 base64 格式
        reader.readAsDataURL(file);
        // 读取成功后的回调
        reader.onloadend = function () {
          // console.log(this.result);
          let result = this.result;
          let img = new Image();
          img.src = result;
          //判断图片是否大于500K,是就直接上传，反之压缩图片
          if (this.result.length <= 500 * 1024) {
            self.headerImage = this.result;
            self.postImg();
          } else {
            img.onload = function () {
              let data = self.compress(img, Orientation);
              self.headerImage = data;
              self.postImg();
            };
          }
        };
      }
    },
    // 压缩图片
    compress(img, Orientation) {
      let canvas = document.createElement("canvas");
      let ctx = canvas.getContext("2d");
      //瓦片canvas
      let tCanvas = document.createElement("canvas");
      let tctx = tCanvas.getContext("2d");
      // let initSize = img.src.length;
      let width = img.width;
      let height = img.height;
      //如果图片大于四百万像素，计算压缩比并将大小压至400万以下
      let ratio;
      if ((ratio = (width * height) / 4000000) > 1) {
        // console.log("大于400万像素");
        ratio = Math.sqrt(ratio);
        width /= ratio;
        height /= ratio;
      } else {
        ratio = 1;
      }
      canvas.width = width;
      canvas.height = height;
      //        铺底色
      ctx.fillStyle = "#fff";
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      //如果图片像素大于100万则使用瓦片绘制
      let count;
      if ((count = (width * height) / 1000000) > 1) {
        // console.log("超过100W像素");
        count = ~~(Math.sqrt(count) + 1); //计算要分成多少块瓦片
        //            计算每块瓦片的宽和高
        let nw = ~~(width / count);
        let nh = ~~(height / count);
        tCanvas.width = nw;
        tCanvas.height = nh;
        for (let i = 0; i < count; i++) {
          for (let j = 0; j < count; j++) {
            tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
            ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
          }
        }
      } else {
        ctx.drawImage(img, 0, 0, width, height);
      }
      //修复ios上传图片的时候 被旋转的问题
      if (Orientation != "" && Orientation != 1) {
        switch (Orientation) {
          case 6: //需要顺时针（向左）90度旋转
            this.rotateImg(img, "left", canvas);
            break;
          case 8: //需要逆时针（向右）90度旋转
            this.rotateImg(img, "right", canvas);
            break;
          case 3: //需要180度旋转
            this.rotateImg(img, "right", canvas); //转两次
            this.rotateImg(img, "right", canvas);
            break;
        }
      }
      //进行最小压缩
      let ndata = canvas.toDataURL("image/jpeg", 0.3);
      tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
      return ndata;
    },
    // 旋转图片
    rotateImg(img, direction, canvas) {
      //最小与最大旋转方向，图片旋转4次后回到原方向
      const min_step = 0;
      const max_step = 3;
      if (img == null) return;
      //img的高度和宽度不能在img元素隐藏后获取，否则会出错
      let height = img.height;
      let width = img.width;
      let step = 2;
      if (step == null) {
        step = min_step;
      }
      if (direction == "right") {
        step++;
        //旋转到原位置，即超过最大值
        step > max_step && (step = min_step);
      } else {
        step--;
        step < min_step && (step = max_step);
      }
      //旋转角度以弧度值为参数
      let degree = (step * 90 * Math.PI) / 180;
      let ctx = canvas.getContext("2d");
      switch (step) {
        case 0:
          canvas.width = width;
          canvas.height = height;
          ctx.drawImage(img, 0, 0);
          break;
        case 1:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, 0, -height);
          break;
        case 2:
          canvas.width = width;
          canvas.height = height;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, -height);
          break;
        case 3:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, 0);
          break;
      }
    },
    //将base64转换为文件
    dataURLtoFile(dataurl) {
      var arr = dataurl.split(","),
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], this.files.name, {
        type: this.files.type
      });
    },
    //这里写接口
    postImg() {
      let file = this.dataURLtoFile(this.headerImage);
      let formData = new window.FormData();
      formData.append("uploadFile", file);
      this.$axios.post(this.upImgUrl, formData, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      }).then(res =>{
        this.bankIdName = res.data.object
        Toast.clear()
        console.log(this.bankIdName,'银行卡')
      })
    },
    // 申请开户
    getLogin(msg) {
      let that = this;
      if(this.bankCardId !== '' && this.bankIdName !== ''){
        console.log(that.global.ws);
        if (that.global.ws && that.global.ws.readyState === 1) {
          console.log("发送信息", msg);
          that.global.ws.onopen = that.onopen()
        }else {
          Toast({
            position:'bottom',
            duration:'2000',
            message:'超出安全时间请重新开户',
            onClose () {
              that.$router.push({
                name:'register',
              })
            }
          })
        }
        that.global.ws.onmessage = function(res) {
          that.websocketonmessage (res)
        };
      }else{
        Toast.fail({
        message:'信息有误或漏填'
      })
      }
    },
    onopen () { // websocket连接后发送数据(send发送)
      let actions;
      let that = this
      let routeData = this.$route.params
      let openKeying = localStorage.getItem('openKeying')
      actions = {
        action: "certification",
        reqID: 9,
        data: {
          name:routeData.name,
          phone:routeData.phone,
          code:routeData.code,
          keying:openKeying,
          idcard:routeData.cardID,
          bank:this.bankCardName,
          bankAccount:this.bankCardId,
          IdCardIs:routeData.idFrontName,
          IdCardBack:routeData.idBackName,
          BankCardImg:this.bankIdName
        }
      } // 请根据实际项目需要进行修改
      console.log(JSON.stringify(actions))
      that.global.ws.send(JSON.stringify(actions))
    },
    websocketonerror () { // 连接建立失败重连
      this.getLogin()
    },
    websocketonmessage (e) { // 数据接收
      this.redata = JSON.parse(e.data)
      console.log(this.redata)
      let _this = this
      if(this.redata.action === "certification"){
        localStorage.removeItem('openKeying')
        if(this.redata.data.code === 0  ){
            localStorage.setItem('status',3)
            Toast({
              type:'success',
              message:this.redata.data.msg,
              duration:1500,
              onClose(){
                _this.$router.push({
                  name:'toBeAudited',
                })
              }
            })
        }else {
          Toast({
            message:_this.redata.data.msg,
            duration:3000,
            onClose(){
              _this.$router.push({
                name:'register',
              })
            }
          })
        }
      // else {
      //     Toast({
      //       message:'提交审核失败，请重新提交',
      //       duration:3000,
      //       onClose(){
      //         _this.$router.push({
      //           name:'register',
      //         })
      //       }
      //     })
      //   }
      }

      // let _this = this
      // if (this.redata.data.ok) {
      //   localStorage.setItem('Flag', 'isLogin')
      //   localStorage.setItem('account', _this.account)
      //   sessionStorage.setItem("disc",this.environment);
      //   this.$router.push({
      //     path:'home'
      //   })
      // } else {
      // }
    },
    websocketclose (e) { // 关闭
      console.log('断开连接', e)
    },
  }
}
</script>

<style scoped lang="scss">
  @import "../../../style/register";
</style>



